     channelScroll() {
      // 
      let width = 11 * 25;
      this.$refs.channelTab.style.width = width + "vw";
      this.$nextTick(() => {
        if (!this.cscroll) {
          this.cscroll = new BScroll(this.$refs.channelWrap, {
            startX: 0,
            click: true,
            scrollX: true,
            scrollY: false,
            eventPassthrough: "vertical"
          });
        } else {
          this.cscroll.refresh();
        }
      });
    },
    systemScroll() {
      // 
      let width = 9 * 40;
      console.log();
      this.$refs.systemTab.style.width = width + "vw";
      this.$nextTick(() => {
        if (!this.sscroll) {
          this.sscroll = new BScroll(this.$refs.systemWrap, {
            startX: 0,
            click: true,
            scrollX: true,
            scrollY: false,
            eventPassthrough: "vertical"
          });
        } else {
          this.sscroll.refresh();
        }
      });
    },
    qItemClick(text) {
       if(!this.GLOBAL.userData.wwid) return

      console.log(text);
      if(text=='HCC'){text=''}
      if(text=='RST'){text='RST'}
      if(text=='MI'){text=''}
      if(text=='Sales Op'){text=''}
      if(text=='HR'){text=''}
      if(text=='Finance'){text=''}
      if(text=='IT'){text=''}
      if(text=='Legal'){text=''}
      if(text=='BQ'){text=''}


      if(text=='//IO//'){text='eProcess'}
      if(text.includes('AMS/Travel/IO/TAM')){text='eProcess'}

      if(text=='/'){text=''}
      if(text.includes('HCP/GO Traffic & Meal')){text=''}

      if(text=='BI/'){text='iDiscover'}
      if(text.includes('BI Reports')){text='iDiscover'}

      if(text=='//'){text='iCenter'}
      if(text.includes('Incentive/Master Data')){text='iCenter'}

      if(text=='/'){text='ConcurWorkday'}
      if(text.includes('Concur & Workday')){text='ConcurWorkday'}

      if(text=='//iPad'){text='iConnect'}
      if(text.includes('Call/Events/iPad')){text='iConnect'}

      if(text=='/'){text='Office 365IRIS'}
      if(text.includes('OneDrive/Outlook/Skype')){text='Office 365IRIS'}

      if(text=='PO//'){text='eMarket PlaceAravo'}
      if(text.includes('E-Market Place')){text='eMarket PlaceAravo'}

      if(text=='CRC'){text='iMedical Review'}
      if(text.includes('CRC Review')){text='iMedical Review'}
      //
      if(text=='iCenter'){text='iCenter'}


      localStorage.setItem(`jassen${this.GLOBAL.userData.wwid}click`, 1);
      this.$router.push({
        path: "/dialogue",
        query: {
          text: text
        }
      });
    },
    handleClick(tab, event) {
      console.log(tab.name);
    },

    // 
    openToolTip() {
      let vm = this;
      const h = this.$createElement;
      this.$msgbox({
        showClose: false,
        message: h("p", null, [
          h(
            "img",
            {
              style: `${
                vm.indexBannerImg
              };height:20vh;width:100%;background-repeat:no-repeat; background-size:100% 100%;-moz-background-size:100% 100%;`
            },
            ""
          ),
          h(
