            " "
          ),
          h(
            "p",
            { style: "margin:0 20px;" },
            " "
          ),
          h("p", { style: "margin:0 20px;" }, "4008889988 "),
          h("p", { style: "margin:0 20px;" }, "cccdesk@its.jnj.com ")
        ]),
        showCancelButton: false,
        confirmButtonText: "",
        callback: action => {
          localStorage.setItem(`jassen${vm.GLOBAL.userData.wwid}message`, true);
        }
      });
    },
       touchStart(e) {
      // 
      this.touchDiffX =
        e.touches[0].clientX - e.srcElement.parentNode.offsetLeft;
      this.touchDiffY =
        e.touches[0].clientY - e.srcElement.parentNode.offsetTop;
    },
    touchEnd(e) {
      this.initX += this.x;
      this.initY += this.y;
    },
    touchMove(e) {
      let move = this.$refs.move;
      if (e.touches.length > 0) {
        this.x = e.touches[0].clientX - this.touchDiffX;
        this.y = e.touches[0].clientY - this.touchDiffY;
        if (
          e.touches[0].clientX > 50 &&
          e.touches[0].clientX < this.clientWidth - 50 &&
          e.touches[0].clientY > 50 &&
          e.touches[0].clientY < this.clientHeight - 50
        ) {
          this.moveEffect = `top:${this.y}px;left:${this.x}px`;
        }
      }
    },
    moveThrottle(e) {
      var _this = this;
      this.throttle(_this.touchMove(e));
    },
    throttle(method) {
      var timer = null;
      return function() {
        var context = this,
          args = arguments;
        clearTimeout(timer);
        timer = setTimeout(function() {
          method.apply(context, args);
        }, 100);
      };
    },
    getFileName(o) {
      var pos = o.lastIndexOf("/");
      return o.substring(pos + 1);
    },
    // 
    bannerClick(str) {
      // alert('bannerClick')
      console.log('bannerClick');
      let imgNum = str.split("img/")[1];
      var url = "";
      if (imgNum.indexOf("01") === 0) {
        url = this.bannerUrl01;
      }
      else if (imgNum.indexOf("02") === 0) {
        url = this.bannerUrl02;
      }
      else {
        url = this.bannerUrl03;
      }

      let vm = this;
      // if (process.env.NODE_ENV === "development") {
      //   url = "/media/" + vm.getFileName(url);
      // }
      console.log(url);
      // alert(url)
      var basicUrl = 'http://janssen.vinfol.com/adapter/janssen/v2/sign?url='
      url = basicUrl + url

      vm.$http.get(url)
        .then(
          response => {
            console.log(response);


            var newUrl = response.bodyText
            // alert(newUrl)

            console.log('newUrl:'+newUrl);
            // window.open(newUrl)
            // location.href = newUrl


            vm.$router.push({
              path: "/pdfView",
              query: {
                url: Base64.encode(newUrl)
              }
            });

          })



    },
