.sysProcessItem {
  /*font-family: -webkit-pictograph;*/
  padding-top: 2px;
  display: inline-block;
  font-size: $font-size-15px;
  min-width: 56vw;
  min-height: 10vh;
  margin: 0.5em;
  border-radius: 0.4em;
  border: 0.1em solid $item-border-color;
  text-align: center;
}
.sysProcessItem div:first-child {
  margin-top: 2.2vh;
}
.sysProcessItem div:nth-child(2) {
  font-size: $font-size-12px;
  font-family: $font-family;
  font-weight: $content-font-weight;
  color: $item-color;
  line-height: 3.2em;
  position: relative;
  top: -0.5em;
}

/*recommended*/
.recommendedContent {
  margin-top: 1em;
}
.recommendedItem {
  display: flex;
  margin-left: -1em;
}
.recommendedItemRight {
  width: 30vw;
  padding: 1em 0;
  text-align: center;
  border: 1px solid $item-border-color;
}
.recommendedItemRight img {
  width: 3.4em;
}
.recommendedItemLeft {
  flex: 1;
  border: 1px solid $item-border-color;
}
.recommendedItemLeftItem {
  padding: 0.5em;
  line-height: 2em;
  border-bottom: 1px solid $item-border-color;
}
.recommendedItemLeftItem i {
  float: right;
  line-height: 2em;
}
.popButton {
  border-radius: 50%;
  width: 5em;
  // margin-left: 3em;
  // position: relative;
  /* right: 0em; */
}
.tip {
  width: 7em;
}

.moveDiv {
  text-align: right;
  position: fixed;
  // bottom: 14em;
  /* left: 70%; */
  z-index: 2;
  // right: 3em;
}

.moveDiv1 {
  bottom: 5em;
  right: 2em;
  width: 5.1em;
  height: 5.1em;
}

.moveDiv2 {
  bottom: 5em;
  right: 2em;
  width: 7em;
  height: 7em;
}
</style>



