                var restItemArrStart = oriArr.length - restItemArrNum
                var restArr = []
                for(var i=restItemArrStart;i<oriArr.length;i++){
                  restArr.push(oriArr[i])
                }

                var doubArr = []
                var newArr = []
                for(var i=0;i<oriArr.length;i++){

