    },

    // 
    searchCapsule(capsule) {
      this.sendMsg(capsule);
    },

    deleteChatting() {
      this.chatting = [];
    },

    // 
    changeCover(cover) {
      this.coverShow = cover;
    },

    // 
    hideCover() {
      if (this.$refs.answer) {
        this.$refs.answer.forEach(item => {
          item.hideCover();
        });
      }
      if (this.$refs.question) {
        this.$refs.question.forEach(item => {
          item.hideCover();
        });
      }

      this.changeCover(false);
    }
  }
};
</script>

<style>
.inputComponents input {
  border-radius: 20px;
}

.inputComponents .el-input--suffix .el-input__inner {
  padding-right: 45px;
}
</style>

<style scoped lang="scss" >
.el-input {
  width: 96%;
}

.dialoguePage {
      background-color: #F5F7FA;
  // margin: 0em 0.8em;
  height: 100vh;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

.dialogueContent {
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling:touch;
  flex: 1;
}

.dialogueContent .dialogueList {
  overflow-y: scroll;
}

.dialogueContent .ask {
  float: right;
  color: $ask-font-color;
  float: right;
  border-radius: 2em 2em 0.5em 2em;

  /*.dialogueContent*/
  clear: both;
  font-size: $font-size-14px;
  margin: 0.5em;
  width: fit-content;
  max-width: 70%;
  min-width: 10%;
  padding: 1.2em;
 background:rgba(24,128,254,1);
box-shadow:0px 4px 8px 0px rgba(210,210,210,0.5);
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
}

.dialogueContent .answer {
  float: left;
  color: $answer-font-color;
  background-color: $answer-background-color;
  border-radius: 0.5em 2em 2em 2em;

  /*.dialogueContent*/
  clear: both;
  font-size: $font-size-14px;
  margin: 0.5em;
  width: fit-content;
  max-width: 70%;
  min-width: 10%;
  padding: 1.2em;
 box-shadow:0px 4px 8px 0px rgba(210,210,210,0.5);
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
}

.tipCard {
  /*margin-left: 1%;*/
  font-size: $font-size-14px;
  width: 100%;
  position: absolute;
  bottom: 3em;
  min-height:100px;
  /*max-height:300px;*/
  overflow: auto;
  // box-shadow: 0em 0.4em 0.8em 0em $box-shadow-color;
  border-radius: 20px 20px 2px 2px;
  background: $answer-background-color;
  border: 0.1rem solid rgba(220,223,230,1);
  box-sizing: border-box;
}

.tipCardItem {
  min-height: 30px;
  line-height: 30px;
  padding: 0.5em 5%;
  border-bottom: 0.1rem solid rgba(220,223,230,1);
  width: 90%;
}

.tipCard div {
  display: inline-block;
}

.inputComponents {
  text-align: center;
  position: sticky;
}

.inputValue {
  bottom: 0.5em;
  margin-bottom: 0.8em;
  position: sticky;
}

.inputValue img {
  height: 30px;
  padding-top: 5px;
  padding-right: 5px;
}

.inputValue i {
  font-size: $font-size-close;
  padding-left: 0.1em;
  vertical-align: middle;
  color: $item-border-color;
}

.srcollHolder::-webkit-scrollbar{
  display:none !important;
  width: 0 !important;
}

.x-scroll {
  overflow-x: scroll;
  -webkit-overflow-scrolling:touch;
  overflow-y: hidden;
  white-space: nowrap;
  // margin: 0.5em -0.8em;
}

.x-scroll::-webkit-scrollbar {
  display:none !important;
  width: 0 !important;
}

.x-scroll div {
  display: inline-block;
  vertical-align: top;
}

.x-scroll::-webkit-scrollbar {
  display: none;
}
.dialogueContent::-webkit-scrollbar {
  display: none;
}
.tipCard::-webkit-scrollbar {
  display: none;
}

.cover {
  position: absolute;
  left: 0;
  top: 0;
  background: $cover-bakcground-color;
  width: 100%;
  height: 100%;
  filter: alpha(opacity=60);
  opacity: 0.6;
  z-index: 990;
}
</style>



